// ==========================================================
// Helpers - Extends
// ==========================================================

// Clearfix

%clearfix {
	&:after {
		content: "";
		display: table;
		clear: both;
	}
}

%clearfixb {
	&:before {
		content: "";
		display: table;
		clear: both;
	}
}


// Screen Reader Text

%srt {
	border: 0;
	clip: rect(0 0 0 0);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
}

%pseudos {
	display: block;
	content: '';
	position: aboslute;
}


