// ==========================================================
// Mixins
// ==========================================================


//Media Queries

@mixin mq($break) {
	@if $break == "small" {
		@media (min-width: $brkpoint-sm) and (max-width: $brkpoint-md) {
			@content;
		}
	}
	@else if $break == "medium" {
		@media (min-width: $brkpoint-md + 1) {
			@content;
		}
	}
	@else if $break == "large" {
		@media (min-width: $brkpoint-lg) {
			@content;
		}
	}
	@else {
		@error "No value could be retrieved for '#{$break}'";
	}
}


// Generate modifier color Clases
@mixin bg-colors($map) {
	@each $theme, $color in $map {
		&--#{$theme} {
			background-color: $color;
		}
	}
}

@mixin img-replace($img, $w, $h, $disp: block) {
	background-image: url('#{path--rel}/#($img}');
	background-repeat: no-repeat;
	width: $w;
	height: $h;
	display: $disp;
}

// Generate pseudo classes mixin

@mixin p-el(
	$el,
	$el-w: null,
	$el-h: null) {

	@if $el == "before" or $el == "after" {
		&:#{$el} {
			@extend %pseudos;
			width: $el-w;
			height: $el-h;
			@content;
		}
	}
	@else {
			@warn "#{$el} is not a valid pseudo-element.";
		}
		
	}


// Adjacent Siblings Margins

@mixin doubly($margin: 1em) {
	& + & {
		margin-left: $margin;
		@content;
	}
}

@mixin transition($speed) {
	-webkit-transition 	: all $speed ease-in-out;
	-moz-transition 	: all $speed ease-in-out;
	-ms-transition 		: all $speed ease-in-out;
	-o-transition 		: all $speed ease-in-out;
	transition 			: all $speed ease-in-out;
}


// Lists

@mixin horizontal() {
  li {
    display: inline-block;
    float: left;
    margin-right: 10px;
    &:last-child { margin-right: 0; }
  }
}

@mixin default-ul() {
  list-style-type: disc;
  margin-bottom: $base-spacing;
  padding-left: $base-spacing;
}

@mixin default-ol() {
  list-style-type: decimal;
  margin-bottom: $base-spacing;
  padding-left: $base-spacing;
}

ul,
ol {
  list-style-type: none;
  margin: 0;
  padding: 0;
  margin-bottom: $base-spacing;

  &%default-ul {
    @include default-ul;
  }

  &%default-ol {
    @include default-ol;
  }

  &%horizontal {
    @include horizontal;
  }
}
